import {
  EDIT_DISTRIBUTION_CENTER,
  FETCH_DISTRIBUTION_CENTER,
  FETCH_DISTRIBUTION_CENTER_TO_STORE,
  FETCH_DISTRIBUTION_CENTER_TO_SUPPLIER,
} from './DistributionCentersActions'

const INITIAL_STATE = {
  items: [],
  items_supplier: [],
  items_store: [],
  roles: [],
  totalSize: 0,
  page: 1,
  sizePerPage: 10,
  filters: [],
  editingDistributionCenters: null,
}

export default (state = INITIAL_STATE, action) => {
  switch (action.type) {
    case FETCH_DISTRIBUTION_CENTER.SUCCESS:
      return {
        ...state,
        items: action.payload.data.items,
        // roles: action.payload.data.filterRoles,
        // totalSize: action.payload.data.totalSize,
        // page: action.payload.data.page,
        // sizePerPage: action.payload.data.sizePerPage,
        // filters: action.payload.data.filters,
      }
    case EDIT_DISTRIBUTION_CENTER.SUCCESS:
      return {
        ...state,
        editingSupplier: action.payload,
      }
    case FETCH_DISTRIBUTION_CENTER_TO_SUPPLIER.SUCCESS:
      return {
        ...state,
        items_supplier: action.payload,
      }
    case FETCH_DISTRIBUTION_CENTER_TO_STORE.SUCCESS:
      return {
        ...state,
        items_store: action.payload,
      }
    default:
      return state
  }
}

export const getSupplierListData = state => {
  return {
    items: state.supplier.items,
    roles: state.supplier.roles,
    totalSize: state.supplier.totalSize,
    page: state.supplier.page,
    sizePerPage: state.supplier.sizePerPage,
  }
}

export const getStoreListData = state => {
  return {
    items: state.store.items,
    roles: state.store.roles,
    totalSize: state.store.totalSize,
    page: state.store.page,
    sizePerPage: state.store.sizePerPage,
  }
}

export const getDistributionCenterListData = state => {
  return {
    items: state.store.items,
    roles: state.store.roles,
    totalSize: state.store.totalSize,
    page: state.store.page,
    sizePerPage: state.store.sizePerPage,
  }
}

export const getEditingDistributionCenters = state => {
  return state.supplier.editingDistributionCenters
}
